<template>
  <v-skeleton-loader :loading="loading" type="table">
    <!-- <v-btn color="primary" text rounded @click="playAudio">test audio</v-btn> -->
    <v-chip-group v-model="selection" value="any">
      <v-chip value="pending" filter outlined>Pending orders</v-chip>
      <v-chip value="confirmed" filter outlined color="success"
        >Confirmed orders</v-chip
      >
      <v-chip value="rejected" filter outlined color="error"
        >Rejected orders</v-chip
      >
    </v-chip-group>
    <v-data-table
      :headers="headers"
      :items="items"
      :single-expand="true"
      :expanded.sync="expanded"
      item-key="_id"
      show-expand
      sort-by="created"
      :search="selection"
      :custom-filter="customFilter"
      :sort-desc="true"
    >
      <template v-slot:item.created="{ item }">
        {{ new Date(item.created) | moment("from", "now") }}
      </template>

      <template v-slot:item.total="{ item }">{{ item.total }} €</template>

      <template v-slot:item.payment="{ item }">
        <v-chip v-if="!item.payment.onlinePayment" small>COD</v-chip>
        <v-chip
          v-else-if="
            item.payment.onlinePayment && item.payment.status === 'pending'
          "
          small
          >pending</v-chip
        >
        <v-chip
          v-else-if="
            item.payment.onlinePayment && item.payment.status === 'paid'
          "
          small
          color="success"
          >paid</v-chip
        >
        <!-- <v-chip v-else-if="item.payment.onlinePayment && item.payment.status === 'failed'" small color="error">paid</v-chip> -->
      </template>

      <template v-slot:item.status="{ item }">
        <v-chip v-if="item.status === 'pending'" small>pending</v-chip>
        <v-chip v-if="item.status === 'confirmed'" small color="success"
          >confirmed</v-chip
        >
        <v-chip v-if="item.status === 'rejected'" small color="error"
          >rejected</v-chip
        >
      </template>

      <!-- <template v-slot:top>
        <v-toolbar flat color="white">
          <v-toolbar-title>Orders</v-toolbar-title>
        </v-toolbar>
      </template> -->

      <template v-slot:expanded-item="{ headers, item }">
        <td id="edpandedTd" :colspan="headers.length">
          <v-data-table
            id="expandedTable"
            class="mb-5"
            :headers="orderItemsHeaders"
            :items="item.items"
            item-key="id"
            hide-default-footer
            disable-pagination
            style="width: 95%; margin: 0 auto"
          >
            <!-- <template v-slot:item.total="{ item }"
              >{{ (item.quantity * item.price).toFixed(2) }} € hello</template
            > -->
            <template v-slot:item.total="{ item }">{{ item.total }} €</template>
            <template v-slot:item.price="{ item }">{{ item.price }} €</template>
            <template v-slot:body.append="{ headers, items }">
              <tr>
                <td
                  style="border-top: 1px solid rgba(0, 0, 0, 0.12)"
                  :colspan="headers.length - 1"
                >
                  <b>Total</b>
                  <span>{{ item.delivery ? " (with delivery)" : "" }}</span>
                </td>
                <td style="border-top: 1px solid rgba(0, 0, 0, 0.12)">
                  <b>{{ item.total }} €</b>
                </td>
              </tr>
            </template>
          </v-data-table>
          <div style="width: 95%; margin: 0 auto; margin-bottom: 20px">
            <v-dialog v-model="dialog" max-width="600" persistent>
              <v-card>
                <v-card-title class="headline">{{ dialogTitle }}</v-card-title>
                <v-card-text>
                  <v-container>
                    <v-row>
                      <v-col cols="12">
                        <v-text-field
                          v-model="computedSubject"
                          label="Subject"
                          placeholder="Your order is on the way"
                          rounded
                          filled
                          hide-details
                        ></v-text-field>
                      </v-col>
                      <v-col v-if="confirmationDialog === true" cols="6">
                        <v-text-field
                          v-model="editedDialog.deliveryTime"
                          type="string"
                          label="Delivery time"
                          placeholder="25 minutes"
                          rounded
                          filled
                          hide-details
                        ></v-text-field>
                      </v-col>
                      <v-col v-if="confirmationDialog === true" cols="6">
                        <v-text-field
                          v-model="editedDialog.totalWithDelivery"
                          type="number"
                          label="Total"
                          :hint="`Order total is: ${item.total} €`"
                          persistent-hint
                          rounded
                          filled
                        ></v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-textarea
                          v-model="computedMessage"
                          filled
                          rounded
                          label="Message"
                          auto-grow
                          hide-details
                        ></v-textarea>
                      </v-col>
                    </v-row>
                  </v-container>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" text rounded @click="close"
                    >Close</v-btn
                  >
                  <v-btn
                    v-if="confirmationDialog === false"
                    color="error"
                    text
                    rounded
                    @click="
                      rejectOrder(item._id, computedSubject, computedMessage)
                    "
                    >Reject order</v-btn
                  >
                  <v-btn
                    v-if="confirmationDialog === true"
                    color="success"
                    text
                    rounded
                    @click="
                      confirmOrder(item._id, computedSubject, computedMessage)
                    "
                    >Confirm order</v-btn
                  >
                </v-card-actions>
              </v-card>
            </v-dialog>

            <v-row>
              <v-expansion-panels
                multiple
                style="margin-bottom: 20px"
                :value="panel"
              >
                <v-col cols="12" md="6" v-if="item.note">
                  <v-expansion-panel>
                    <v-expansion-panel-header>Note</v-expansion-panel-header>
                    <v-expansion-panel-content>{{
                      item.note
                    }}</v-expansion-panel-content>
                  </v-expansion-panel>
                </v-col>

                <v-col cols="12" md="6" v-if="!item.note">
                  <v-expansion-panel disabled>
                    <v-expansion-panel-header>Note</v-expansion-panel-header>
                    <v-expansion-panel-content
                      >Customer has not provided a
                      note.</v-expansion-panel-content
                    >
                  </v-expansion-panel>
                </v-col>

                <v-col cols="12" md="6">
                  <v-expansion-panel>
                    <v-expansion-panel-header
                      >Detailed customer info</v-expansion-panel-header
                    >
                    <v-expansion-panel-content>
                      <v-card
                        class="mx-auto"
                        flat
                        rounded
                        color="rgba(0, 0, 0, 0.06)"
                        style="border-radius: 28px !important"
                      >
                        <v-list-item three-line>
                          <v-list-item-avatar color="#0360ec">
                            <span class="white--text">{{
                              item.customer.name
                                .split(" ")
                                .map((n) => n[0])
                                .join("")
                            }}</span>
                          </v-list-item-avatar>
                          <v-list-item-content>
                            <v-list-item-title class="">{{
                              item.customer.name
                            }}</v-list-item-title>
                            <v-list-item-subtitle>{{
                              item.customer.email
                            }}</v-list-item-subtitle>
                            <v-list-item-subtitle class="mb-2">{{
                              item.customer.mobile
                            }}</v-list-item-subtitle>
                            <v-list-item-subtitle v-if="item.customer.address"
                              >{{ item.customer.address.street }},
                              {{ item.customer.address.zip }}
                              {{
                                item.customer.address.city
                              }}</v-list-item-subtitle
                            >
                            <v-list-item-subtitle v-if="item.customer.ip"
                              >{{ item.customer.ip.ipAddress }} ({{
                                item.customer.ip.ipAddressLocation
                              }})</v-list-item-subtitle
                            >
                          </v-list-item-content>
                        </v-list-item>
                      </v-card>
                    </v-expansion-panel-content>
                  </v-expansion-panel>
                </v-col>
              </v-expansion-panels>
            </v-row>

            <v-btn
              v-if="item.status === 'pending'"
              color="success"
              text
              rounded
              @click="
                dialog = true;
                confirmationDialog = true;
                editedDialog.totalWithDelivery = item.total;
              "
              >Confirm order</v-btn
            >
            <v-btn
              v-if="item.status === 'pending'"
              color="error"
              text
              rounded
              @click="
                dialog = true;
                confirmationDialog = false;
              "
              >Reject order</v-btn
            >
          </div>
        </td>
      </template>
    </v-data-table>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ snackbarText }}
      <v-btn text small rounded @click="stopAudio()">Ok</v-btn>
    </v-snackbar>
  </v-skeleton-loader>
</template>

<script>
import restaurantService from "../services/restaurant.service";
import orderService from "../services/order.service";
import { API_URL } from "../api.js";

export default {
  props: ["searchValue"],
  data: () => ({
    selection: "pending",
    a: "20 minutes",
    b: 7.99,
    c:
      "Hello, your order is on the way to your home. It will arrive in DELIVERY_TIME. Total with shipping is TOTAL_WITH_DELIVERY €. Thank you for your order. Bon appetite.",
    loading: true,
    dialog: false,
    snackbar: false,
    snackbarText: "",
    timeout: 0,
    confirmationDialog: Boolean,
    expanded: [],
    headers: [
      { text: "ID", value: "id", sortable: false },
      { text: "Created", value: "created" },
      { text: "Customer", value: "customer.email" },
      { text: "Delivery", value: "customer.address.raw", sortable: false },
      { text: "Mobile", value: "customer.mobile", sortable: false },
      { text: "Total", value: "total" },
      { text: "Payment", value: "payment", sortable: false },
      { text: "Status", value: "status", sortable: false },
      { text: "", value: "data-table-expand" },
    ],
    orderItemsHeaders: [
      { text: "ID", value: "id" },
      { text: "Name", value: "name" },
      { text: "Options", value: "options" },
      { text: "Price", value: "price" },
      { text: "Quantity", value: "quantity" },
      { text: "Total", value: "total" },
    ],
    items: [],
    panel: [0],
    defaultMessages: {
      confirmation: {
        subject: "",
        message: "",
      },
      rejection: {
        subject: "",
        message: "",
      },
    },
    defaultDialog: {
      subject: null,
      deliveryTime: "",
      totalWithDelivery: null,
      message: null,
    },
    editedDialog: {
      subject: null,
      deliveryTime: "",
      totalWithDelivery: null,
      message: null,
    },
    sse: new EventSource(
      `${API_URL}/order/stream?token=${localStorage.getItem(
        "token"
      )}&restaurantId=${localStorage.getItem("restaurantId")}`,
      { withCredentials: true }
    ),
    audio: new Audio(require("../assets/notification.mp3")),
  }),
  watch: {
    selection(v) {
      if (!v) {
        this.selection = "any";
      }
    },
  },
  computed: {
    dialogTitle() {
      return this.confirmationDialog === true
        ? "Confirm order"
        : "Reject order";
    },
    // computedConfirmationMessage: {
    //   get: function() {
    //     if (this.confirmationDialog) {
    //       let copy = this.editedDialog.message;
    //       if (this.editedDialog.deliveryTime)
    //         copy = copy.replace(
    //           "DELIVERY_TIME",
    //           this.editedDialog.deliveryTime
    //         );
    //       if (this.editedDialog.totalWithDelivery)
    //         copy = copy.replace(
    //           "TOTAL_WITH_DELIVERY",
    //           this.editedDialog.totalWithDelivery
    //         );
    //       return copy;
    //     } else return "";
    //   },
    //   set: function(newValue) {
    //     if (this.confirmationDialog) {
    //       let copy = newValue;
    //       if (this.editedDialog.deliveryTime)
    //         copy = copy.replace(
    //           this.editedDialog.deliveryTime,
    //           "DELIVERY_TIME"
    //         );
    //       if (this.editedDialog.totalWithDelivery)
    //         copy = copy.replace(
    //           this.editedDialog.totalWithDelivery,
    //           "TOTAL_WITH_DELIVERY"
    //         );
    //       return (this.editedDialog.message = copy);
    //     } else return "";
    //   }
    // },
    computedMessage: {
      get: function () {
        if (this.confirmationDialog) {
          if (this.editedDialog.message !== null) {
            let copy = this.editedDialog.message;
            if (this.editedDialog.deliveryTime)
              copy = copy.replace(
                "DELIVERY_TIME",
                this.editedDialog.deliveryTime
              );
            if (this.editedDialog.totalWithDelivery)
              copy = copy.replace(
                "TOTAL_WITH_DELIVERY",
                this.editedDialog.totalWithDelivery
              );
            return copy;
          } else {
            let copy = this.defaultMessages.confirmation.message;
            if (this.editedDialog.deliveryTime)
              copy = copy.replace(
                "DELIVERY_TIME",
                this.editedDialog.deliveryTime
              );
            if (this.editedDialog.totalWithDelivery)
              copy = copy.replace(
                "TOTAL_WITH_DELIVERY",
                this.editedDialog.totalWithDelivery
              );
            return copy;
          }
        } else {
          if (this.editedDialog.message !== null)
            return this.editedDialog.message;
          else return this.defaultMessages.rejection.message;
        }
      },
      set: function (newValue) {
        if (this.confirmationDialog) {
          let copy = newValue;
          if (this.editedDialog.deliveryTime)
            copy = copy.replace(
              this.editedDialog.deliveryTime,
              "DELIVERY_TIME"
            );
          if (this.editedDialog.totalWithDelivery)
            copy = copy.replace(
              this.editedDialog.totalWithDelivery,
              "TOTAL_WITH_DELIVERY"
            );
          return (this.editedDialog.message = copy);
        } else return (this.editedDialog.message = newValue);
      },
    },
    computedSubject: {
      get: function () {
        if (this.confirmationDialog) {
          if (this.editedDialog.subject !== null)
            return this.editedDialog.subject;
          else return this.defaultMessages.confirmation.subject;
        } else {
          if (this.editedDialog.subject !== null)
            return this.editedDialog.subject;
          else return this.defaultMessages.rejection.subject;
        }
      },
      set: function (newValue) {
        return (this.editedDialog.subject = newValue);
      },
    },
  },

  created() {
    // fetch the data when the view is created and the data is
    // already being observed
    this.initializeSse();
    this.initialize();
    // this.defaultDialog.message =
    //   "Hello, your order is on the way to your home. It will arrive in DELIVERY_TIME. Total with shipping is TOTAL_WITH_DELIVERY €. Thank you for your order. Bon appetite.";
    // this.editedDialog.message =
    //   "Hello, your order is on the way to your home. It will arrive in DELIVERY_TIME. Total with shipping is TOTAL_WITH_DELIVERY €. Thank you for your order. Bon appetite.";
  },

  methods: {
    initializeSse() {
      this.sse.onmessage = (e) => {
        console.log("SSE message");
        const data = JSON.parse(e.data);
        if (Array.isArray(data)) this.items = data;
        else {
          const index = this.items.findIndex((item) => item._id === data._id);
          if (index === -1) {
            this.items.push(data);
            this.snackbarText = `Nová objednávka č. ${data.id} v hodnote ${data.total}€.`;
            this.playAudio();
          } else {
            this.items[index].payment = data.payment;
            this.snackbarText = `Platba pre objednávku č. ${data.id} (v hodnote ${data.total}€) bola prijatá.`;
            this.playAudio();
          }
        }
        this.loading = false;
      };
    },
    async initialize() {
      const resp = await restaurantService.getRestaurant(
        localStorage.getItem("restaurantId"),
        "defaultMessages"
      );
      this.defaultMessages.confirmation.subject = resp.confirmation.subject;
      this.defaultMessages.confirmation.message = resp.confirmation.message;
      this.defaultMessages.rejection.subject = resp.rejection.subject;
      this.defaultMessages.rejection.message = resp.rejection.message;

      // this.defaultDialog.subject = resp.confirmation.subject;
      // this.editedDialog.subject = resp.confirmation.subject;
      // this.defaultDialog.message = resp.confirmation.message;
      // this.editedDialog.message = resp.confirmation.message;
    },

    customFilter(value, search, item) {
      // return (
      //   value != null &&
      //   search != null &&
      //   item.status != null &&
      //   item.status === search
      // );
      if (search != "any")
        return (
          value != null &&
          search != null &&
          item.status != null &&
          item.status === search &&
          (this.searchValue === "" ||
            item.id.includes(this.searchValue) ||
            item.customer.email.includes(this.searchValue))
        );
      return (
        item.id.includes(this.searchValue) ||
        item.customer.email.includes(this.searchValue)
      );
    },

    async confirmOrder(orderId, subject, message) {
      if (window.confirm("Are you sure you want to confirm this order?")) {
        const resp = await orderService.confirmOrder(
          localStorage.getItem("restaurantId"),
          orderId,
          subject,
          message
        );
        const index = this.items.findIndex((item) => item._id === resp._id);
        console.log(index, this.items[index].status, resp.status);
        this.items[index].status = resp.status;
        this.close();
      }
    },

    async rejectOrder(orderId, subject, message) {
      if (window.confirm("Are you sure you want to reject this order?")) {
        const resp = await orderService.rejectOrder(
          localStorage.getItem("restaurantId"),
          orderId,
          subject,
          message
        );
        const index = this.items.findIndex((item) => item._id === resp._id);
        this.items[index].status = resp.status;
        this.close();
      }
    },

    playAudio() {
      this.snackbar = true;
      this.audio.pause();
      this.audio.loop = true;
      this.audio.play();
    },

    stopAudio() {
      this.snackbar = false;
      this.audio.loop = false;
      this.audio.pause();
    },

    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedDialog = Object.assign({}, this.defaultDialog);
      }, 300);
    },
  },
  beforeRouteLeave(to, from, next) {
    this.sse.close();
    next();
  },
};
</script>

<style>
.v-data-table tbody tr.expanded.expanded__content {
  box-shadow: none !important;
}

.v-data-table tbody tr.v-data-table__expanded__content {
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
}

#expandedTable tbody tr td {
  border-bottom: none;
}

.theme--light.v-data-table tbody tr.expanded.expanded__content:hover,
#edpandedTd * :hover {
  background: none;
}
</style>
