<template>
  <div class="overlay">
    <div class="form-wrapper">
      <v-img
        class="mb-8"
        style="margin: 0 auto"
        max-width="100px"
        src="../assets/piknik-logo-white.svg"
        alt="Piknik"
      />
      <h2 class="white--text mb-2">Zmena hesla</h2>
      <p class="white--text mb-8">
        Zvoľte si nové heslo a následne ho potvrďte.
      </p>
      <v-form ref="form" v-model="valid">
        <v-text-field
          dark
          v-model="password1"
          :rules="[rules.required, rules.min, rules.max, rules.password]"
          :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="show1 = !show1"
          :type="show1 ? 'text' : 'password'"
          label="Heslo"
          rounded
          dense
          filled
        ></v-text-field>
        <v-text-field
          dark
          v-model="password2"
          :rules="[
            rules.required,
            rules.min,
            rules.max,
            rules.password,
            checkSamePasswords,
          ]"
          :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="show2 = !show2"
          :type="show2 ? 'text' : 'password'"
          label="Heslo"
          rounded
          dense
          filled
        ></v-text-field>
        <v-btn
          v-on:click="changePassword"
          dark
          color="white"
          depressed
          rounded
          :disabled="!valid || snackbar"
          >Zmeniť heslo</v-btn
        >
      </v-form>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ snackbarText }}
      <v-btn v-if="respStatus === 200" text small rounded @click="$router.push('/login')" color="white"
        >Prihlásenie</v-btn
      >
      <v-btn v-else-if="respStatus === 400" text small rounded @click="$router.push('/forgotpassword')" color="white"
        >Skúsiť znovu</v-btn
      >
    </v-snackbar>
  </div>
</template>

<script>
import userService from "../services/user.service";

export default {
  data: () => ({
    drawer: null,
    valid: true,
    show1: false,
    password1: "",
    show2: false,
    password2: "",
    rules: {
      required: (value) => !!value || "Povinné pole.",
      min: (v) => v.length >= 8 || "Minimálne 8 znakov.",
      max: (v) => v.length <= 48 || "Maximálne 48 znakov.",
      password: (v) =>
        /^(?=.*([A-Z]){1,})(?=.*[!@#$&*]{1,})(?=.*[0-9]{1,})(?=.*[a-z]{1,}).{8,48}$/.test(
          v
        ) ||
        "Musí obsahovať 1 malé a 1 veľké písmeno, 1 číslo a 1 špeciálny znak.",
    },
    snackbar: false,
    snackbarText: "",
    respStatus: null,
    timeout: 0,
  }),
  computed: {
    checkSamePasswords: function () {
      return this.password1 === this.password2 || "Heslá sa nezhodujú.";
    },
  },
  methods: {
    async changePassword() {
      const token = this.$route.query.token;
      try {
        const resp = await userService.changePassword(token, this.password1);
        this.respStatus = 200
        this.snackbarText = "Heslo bolo zmenené, prosím prihláste sa."
        this.snackbar = true;
      } catch {
        this.respStatus = 400
        this.snackbarText = 'Platnosť linku vypršala, opakujte obnovenie hesla.'
        this.snackbar = true;
      }
    },
  },
};
</script>

<style scoped>
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #007bff;
  z-index: 10;
  display: flex;
  align-items: center;
  text-align: center;
}

.form-wrapper {
  width: 350px;
  height: auto;
  margin: 0 auto;
}

@media (max-width: 600px) {
  .form-wrapper {
    padding: 32px;
  }
}

/deep/ .v-input__slot {
  background: rgba(255, 255, 255, 0.2) !important;
}

/deep/ .v-btn {
  color: #007bff !important;
}

/deep/ .v-application .error--text {
  color: black !important;
  caret-color: black !important;
}
</style>