<template>
  <v-skeleton-loader :loading="loading" type="table">
    <v-chip-group
      v-model="selection"
      column
      active-class="active-category-class"
    >
      <v-chip
        pill
        v-for="category in categories"
        :key="category._id"
        :value="category._id"
        :color="category.color"
      >
        <v-avatar left color="#00000015">
          <v-icon small @click="editCategory(category)">mdi-pencil</v-icon>
        </v-avatar>
        {{ category.name }}
      </v-chip>
      <v-dialog v-model="dialogCategory" max-width="400" persistent>
        <template v-slot:activator="{ on }">
          <v-icon v-on="on" title="Add new category">mdi-plus</v-icon>
        </template>
        <v-card>
          <v-card-title class="headline">{{ categoryFormTitle }}</v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedCategory.name"
                    label="Name"
                    placeholder="Drinks"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedCategory.rank"
                    type="number"
                    label="Rank"
                    placeholder="1"
                    rounded
                    filled
                    hide-details
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-color-picker
                    v-model="editedCategory.color"
                    hide-canvas
                    hide-inputs
                    hide-mode-switch
                    mode="hexa"
                    class="mx-auto"
                  ></v-color-picker>
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" text rounded @click="dialogCategory = false"
              >Close</v-btn
            >
            <v-btn
              color="error"
              text
              rounded
              v-if="categoryEditedIndex > -1"
              @click="deleteCategory(editedCategory)"
              >Remove category</v-btn
            >
            <v-btn color="success" text rounded @click="saveCategory()"
              >Save</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-chip-group>
    <v-data-table
      :headers="headers"
      :items="items"
      sort-by="id"
      :search="selection"
      :custom-filter="filterOnlyCapsText"
    >
      <template v-slot:body="{ items }">
        <tbody>
          <tr v-for="item in items" :key="item.name">
            <td>
              <v-chip
                v-if="categories.find((x) => x._id === item.category)"
                small
                :color="categories.find((x) => x._id === item.category).color"
                >{{ item.id }}</v-chip
              >
              <v-chip
                v-if="!categories.find((x) => x._id === item.category)"
                small
                >{{ item.id }}</v-chip
              >
            </td>
            <td>
              <v-icon
                x-small
                :color="item.status ? 'success' : 'error'"
                class="mr-2"
              >
                mdi-checkbox-blank-circle </v-icon
              >{{ item.name }}
            </td>
            <td>
              <v-chip
                v-for="synonym in item.synonyms"
                :key="synonym"
                class="ma-1"
                small
                >{{ synonym }}</v-chip
              >
            </td>
            <td>{{ item.description }}</td>
            <td>{{ item.amount }}</td>
            <td>{{ item.price }} €</td>
            <td>
              <v-icon small class="mr-2" @click="editItem(item)"
                >mdi-pencil</v-icon
              >
              <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </td>
          </tr>
        </tbody>
      </template>

      <template v-slot:top>
        <v-toolbar flat color="white">
          <!-- <v-toolbar-title>Menu card</v-toolbar-title> -->
          <div class="flex-grow-1"></div>
          <v-dialog v-model="dialog" max-width="800px" persistent>
            <template v-slot:activator="{ on }">
              <v-btn color="primary" text rounded class="mb-2" v-on="on"
                >New Item</v-btn
              >
            </template>
            <v-card>
              <v-card-title class="headline">{{ formTitle }}</v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12">
                      <span class="mt-0 pt-0 mr-4 d-inline float-left"
                        >Status:</span
                      >
                      <v-switch
                        class="mt-0 pt-0 mr-4 d-inline float-left"
                        v-model="editedItem.status"
                        :label="editedItem.status ? 'Enabled' : 'Disabled'"
                        color="primary"
                        hide-details
                      ></v-switch>
                    </v-col>
                    <v-col cols="2">
                      <v-text-field
                        v-model="editedItem.id"
                        type="number"
                        label="ID"
                        placeholder="21"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="4">
                      <v-select
                        :items="categories"
                        item-text="name"
                        item-value="_id"
                        v-model="editedItem.category"
                        label="Kategória"
                        placeholder="Soups"
                        filled
                        rounded
                        clearable
                        hide-details
                      ></v-select>
                    </v-col>
                    <v-col cols="6">
                      <v-text-field
                        v-model="editedItem.name"
                        label="Name"
                        placeholder="Bryndzové halušky"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-combobox
                        v-model="editedItem.synonyms"
                        label="Add some synonyms"
                        multiple
                        small-chips
                        rounded
                        filled
                        placeholder="halušky"
                        hint="Press enter to create a new one."
                        append-icon
                        persistent-hint
                      ></v-combobox>
                    </v-col>
                    <v-col cols="12">
                      <v-autocomplete
                        v-model="editedItem.options"
                        :items="options"
                        label="Options"
                        item-text="name"
                        item-value="_id"
                        filled
                        rounded
                        chips
                        small-chips
                        deletable-chips
                        multiple
                        hide-details
                      ></v-autocomplete>
                    </v-col>
                    <v-col cols="12">
                      <v-text-field
                        v-model="editedItem.description"
                        label="Description"
                        placeholder="halušky, bryndza, slanina"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12">
                      <v-autocomplete
                        v-model="editedItem.allergens"
                        :items="allergens"
                        label="Allergens"
                        filled
                        rounded
                        chips
                        small-chips
                        deletable-chips
                        multiple
                        hide-details
                      ></v-autocomplete>
                    </v-col>
                    <v-col cols="4">
                      <v-text-field
                        v-model="editedItem.amount"
                        label="Amount"
                        placeholder="400 g"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="4">
                      <v-text-field
                        v-model="editedItem.price"
                        type="number"
                        prefix="€"
                        label="Price"
                        placeholder="5,00"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="4">
                      <v-text-field
                        v-model="editedItem.imageUrl"
                        label="Image URL"
                        placeholder="https://domain.com/image.jpg"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <div class="flex-grow-1"></div>
                <v-btn color="primary" text rounded @click="close">Close</v-btn>
                <v-btn color="success" text rounded @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </v-skeleton-loader>
</template>

<script>
import restaurantService from "../services/restaurant.service";

export default {
  data: () => ({
    loading: true,
    dialogCategory: false,
    selection: null,
    dialog: false,
    categories: [],
    categoryEditedIndex: -1,
    defaultCategory: {
      name: "",
      rank: null,
      color: "",
    },
    editedCategory: {
      name: "",
      rank: null,
      color: "",
    },
    headers: [
      { text: "ID", value: "id" },
      { text: "Name", value: "name" },
      { text: "Synonyms", value: "synonyms", sortable: false },
      { text: "Description", value: "description", sortable: false },
      { text: "Amount", value: "amount", sortable: false },
      { text: "Price", value: "price" },
      { text: "Actions", value: "action", sortable: false },
    ],
    items: [],
    options: [],
    allergens: [
      "1. Obilniny",
      "2. Kôrovce",
      "3. Vajcia",
      "4. Ryby",
      "5. Arašidy",
      "6. Sójové zrná",
      "7. Mlieko",
      "8. Orechy",
      "9. Zeler",
      "10. Horčica",
      "11. Sezamové semená",
      "12. Oxid siričitý a siričitany",
      "13. Vlčí bôb",
      "14. Mäkkýše",
    ],
    // allergens: [
    //   { id: 1, name: "1. Obilniny" },
    //   { id: 2, name: "2. Kôrovce" },
    //   { id: 3, name: "3. Vajcia" },
    //   { id: 4, name: "4. Ryby" },
    //   { id: 5, name: "5. Arašidy" },
    //   { id: 6, name: "6. Sójové zrná" },
    //   { id: 7, name: "7. Mlieko" },
    //   { id: 8, name: "8. Orechy" },
    //   { id: 9, name: "9. Zeler" },
    //   { id: 10, name: "10. Horčica" },
    //   { id: 11, name: "11. Sezamové semená" },
    //   { id: 12, name: "12. Oxid siričitý a siričitany" },
    //   { id: 13, name: "13. Vlčí bôb" },
    //   { id: 14, name: "14. Mäkkýše" },
    // ],
    editedIndex: -1,
    editedItem: {
      status: true,
      id: "",
      name: "",
      category: "",
      synonyms: [],
      allergens: [],
      options: [],
      description: "",
      amount: "",
      price: null,
      imageUrl: "",
    },
    defaultItem: {
      status: true,
      id: "",
      name: "",
      category: "",
      synonyms: [],
      allergens: [],
      options: [],
      description: "",
      amount: "",
      price: null,
      imageUrl: "",
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Add new dish" : "Edit dish";
    },
    categoryFormTitle() {
      return this.categoryEditedIndex === -1
        ? "Add new category"
        : "Edit category";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogCategory(val) {
      val || this.closeCategory();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      // const resp = await restaurantService.getMenu(
      //   localStorage.getItem("restaurantId")
      // );
      const resp = await restaurantService.getRestaurant(
        localStorage.restaurantId,
        "dishes,options,categories"
      );
      this.items = resp.dishes;
      this.options = resp.options;
      this.categories = resp.categories.sort(
        (a, b) => parseFloat(a.rank) - parseFloat(b.rank)
      );
      this.loading = false;
    },

    filterOnlyCapsText(value, search, item) {
      return (
        value != null &&
        search != null &&
        item.category != null &&
        item.category === search
      );
    },

    editCategory(category) {
      this.categoryEditedIndex = this.categories.indexOf(category);
      this.editedCategory = Object.assign({}, category);
      this.dialogCategory = true;
    },

    editItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    async deleteCategory(category) {
      if (window.confirm("Are you sure you want to delete this category?")) {
        const resp = await restaurantService.removeCategory(
          localStorage.getItem("restaurantId"),
          category._id
        );
        this.categories = resp.sort(
          (a, b) => parseFloat(a.rank) - parseFloat(b.rank)
        );
        this.closeCategory();
      }
    },

    async deleteItem(item) {
      if (window.confirm("Are you sure you want to delete this item?")) {
        const resp = await restaurantService.removeDish(
          localStorage.getItem("restaurantId"),
          item._id
        );
        this.items = resp;
      }
    },

    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },

    closeCategory() {
      this.dialogCategory = false;
      setTimeout(() => {
        this.editedCategory = Object.assign({}, this.defaultCategory);
        this.categoryEditedIndex = -1;
      }, 300);
    },

    async save() {
      if (this.editedIndex > -1) {
        const resp = await restaurantService.editDish(
          localStorage.getItem("restaurantId"),
          this.editedItem
        );
        this.items = resp;
      } else {
        const resp = await restaurantService.addDish(
          localStorage.getItem("restaurantId"),
          this.editedItem
        );
        this.items = resp;
      }
      this.close();
    },

    async saveCategory() {
      if (this.categoryEditedIndex > -1) {
        const resp = await restaurantService.editCategory(
          localStorage.getItem("restaurantId"),
          this.editedCategory
        );
        this.categories = resp.sort(
          (a, b) => parseFloat(a.rank) - parseFloat(b.rank)
        );
      } else {
        const resp = await restaurantService.addCategory(
          localStorage.getItem("restaurantId"),
          this.editedCategory
        );
        this.categories = resp.sort(
          (a, b) => parseFloat(a.rank) - parseFloat(b.rank)
        );
      }
      this.closeCategory();
    },
  },
};
</script>

<style>
.active-category-class {
  font-weight: bold;
}

.v-data-table .v-toolbar__content {
  float: left;
  padding: 0;
}
</style>