<template>
  <v-skeleton-loader :loading="loading" type="table">
    <v-data-table :headers="headers" :items="items">
      <template v-slot:body="{ items }">
        <tbody>
          <tr v-for="item in items" :key="item.name">
            <td>{{ item.name }}</td>
            <td>{{ item.price }} €</td>
            <td>
              <v-icon small class="mr-2" @click="editItem(item)"
                >mdi-pencil</v-icon
              >
              <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </td>
          </tr>
        </tbody>
      </template>

      <template v-slot:top>
        <v-toolbar flat color="white">
          <div class="flex-grow-1"></div>
          <v-dialog v-model="dialog" max-width="800px" persistent>
            <template v-slot:activator="{ on }">
              <v-btn color="primary" text rounded class="mb-2" v-on="on"
                >New zone</v-btn
              >
            </template>
            <v-card>
              <v-card-title class="headline">{{ formTitle }}</v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="8">
                      <v-text-field
                        v-model="editedItem.name"
                        label="Zone name"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="4">
                      <v-text-field
                        v-model="editedItem.price"
                        type="number"
                        label="Delivery price"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <div class="flex-grow-1"></div>
                <v-btn color="primary" text rounded @click="close">Close</v-btn>
                <v-btn color="success" text rounded @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </v-skeleton-loader>
</template>

<script>
import restaurantService from "../services/restaurant.service";

export default {
  data: () => ({
    loading: true,
    dialog: false,
    headers: [
      { text: "Name", value: "name" },
      { text: "Delivery price", value: "price" },
      { text: "Actions", value: "action", sortable: false },
    ],
    items: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      price: null,
    },
    defaultItem: {
      name: "",
      price: null,
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Add new zone" : "Edit zone";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      const resp = await restaurantService.getRestaurant(
        localStorage.getItem("restaurantId"),
        "deliveryOptions"
      );
      this.items = resp.delivery.zones;
      this.loading = false;
    },
    editItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    async deleteItem(item) {
      if (window.confirm("Are you sure you want to delete this zone?")) {
        const resp = await restaurantService.removeZone(
          localStorage.getItem("restaurantId"),
          item._id
        );
        this.items = resp;
      }
    },
    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    async save() {
      if (this.editedIndex > -1) {
        const resp = await restaurantService.editZone(
          localStorage.getItem("restaurantId"),
          this.editedItem
        );
        this.items = resp;
      } else {
        const resp = await restaurantService.addZone(
          localStorage.getItem("restaurantId"),
          this.editedItem
        );
        this.items = resp;
      }
      this.close();
    },
  },
};
</script>

<style scoped>
.active-category-class {
  font-weight: bold;
}

.v-data-table .v-toolbar__content {
  float: left;
  padding: 0;
}

.v-btn--round .v-btn__content .v-icon {
  color: unset !important;
}
</style>