<template>
  <div class="overlay">
    <div class="form-wrapper">
      <v-img
        class="mb-8"
        style="margin: 0 auto"
        max-width="100px"
        src="../assets/piknik-logo-white.svg"
        alt="Piknik"
      />
      <h2 class="white--text mb-2">Zabudnuté heslo?</h2>
      <p class="white--text mb-8">
        Ak chcete obnoviť svoje heslo, zadajte e-mailovú adresu, ktorú používate
        na prihlásenie.
      </p>
      <v-form ref="form" v-model="valid">
        <v-text-field
          dark
          v-model="email"
          :rules="[rules.required, rules.email]"
          label="Email"
          type="email"
          rounded
          dense
          filled
        ></v-text-field>
        <v-btn
          v-on:click="forgotPassword"
          dark
          color="white"
          depressed
          rounded
          :disabled="!valid || snackbar"
          >Obnoviť heslo</v-btn
        >
      </v-form>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ snackbarText }}
      <!-- <v-btn text small rounded @click="reload()" color="white"
        >Ok</v-btn
      > -->
    </v-snackbar>
  </div>
</template>

<script>
import userService from "../services/user.service";

export default {
  data: () => ({
    drawer: null,
    valid: true,
    rules: {
      required: (value) => !!value || "Povinné pole.",
      email: (v) => /.+@.+\..+/.test(v) || "Email musí byť v správnom formáte.",
    },
    email: "",
    snackbar: false,
    snackbarText: "V prípade že účet existuje, odoslali sme obnovovací email.",
    timeout: 0,
  }),
  methods: {
    async forgotPassword() {
      const resp = await userService.forgotPassword(this.email);
      this.snackbar = true;
    },
    // reload() {
    //   this.email = "";
    //   this.snackbar = false;
    // },
  },
};
</script>

<style scoped>
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #007bff;
  z-index: 10;
  display: flex;
  align-items: center;
  text-align: center;
}

.form-wrapper {
  width: 350px;
  height: auto;
  margin: 0 auto;
}

@media (max-width: 600px) {
  .form-wrapper {
    padding: 32px;
  }
}

/deep/ .v-input__slot {
  background: rgba(255, 255, 255, 0.2) !important;
}

/deep/ .v-btn {
  color: #007bff !important;
}

/deep/ .v-application .error--text {
  color: black !important;
  caret-color: black !important;
}
</style>