<template>
  <v-skeleton-loader :loading="loading" type="table">
    <v-data-table :headers="headers" :items="items">
      <template v-slot:body="{ items }">
        <tbody>
          <tr v-for="item in items" :key="item.name">
            <td>{{ item.name }}</td>
            <td>{{ item.minimumSelections }}</td>
            <td>{{ item.maximumSelections }}</td>
            <td>
              <v-icon small class="mr-2" @click="editItem(item)"
                >mdi-pencil</v-icon
              >
              <v-icon small @click="deleteItem(item)">mdi-delete</v-icon>
            </td>
          </tr>
        </tbody>
      </template>

      <template v-slot:top>
        <v-toolbar flat color="white">
          <!-- <v-toolbar-title>Menu card</v-toolbar-title> -->
          <div class="flex-grow-1"></div>
          <v-dialog v-model="dialog" max-width="800px" persistent>
            <template v-slot:activator="{ on }">
              <v-btn color="primary" text rounded class="mb-2" v-on="on"
                >New option</v-btn
              >
            </template>
            <v-card>
              <v-card-title class="headline">{{ formTitle }}</v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="6">
                      <v-text-field
                        v-model="editedItem.name"
                        label="Name"
                        placeholder="Príloha na výber"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="3">
                      <v-text-field
                        v-model="editedItem.minimumSelections"
                        type="number"
                        label="Minimum selections"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="3">
                      <v-text-field
                        v-model="editedItem.maximumSelections"
                        type="number"
                        label="Maximum selections"
                        rounded
                        filled
                        hide-details
                      ></v-text-field>
                    </v-col>
                    <v-col cols="1">
                      <v-btn
                        icon
                        color="#0360ec"
                        @click="addOptionValue()"
                      >
                        <v-icon>mdi-plus</v-icon>
                      </v-btn>
                    </v-col>
                    <v-col cols="11">
                      <v-expansion-panels accordion flat>
                        <v-expansion-panel
                          v-for="(value, i) in editedItem.values"
                          :key="i"
                        >
                          <v-expansion-panel-header>
                            <template v-slot:actions>
                              <v-btn
                                icon
                                small
                                color="error"
                                class="mr-2"
                                @click.native.stop="removeOptionValue(value)"
                              >
                                <v-icon color="error">mdi-minus</v-icon>
                              </v-btn>
                              <v-icon>$expand</v-icon>
                            </template>
                            <v-row no-gutters>
                              <v-col cols="6">{{ value.name }}</v-col>
                              <v-col cols="3" class="text--secondary">
                                Price: {{ value.price }}€
                              </v-col>
                              <v-col cols="3" class="text--secondary">
                                Maximum: {{ value.maximumAmount }}
                              </v-col>
                            </v-row>
                          </v-expansion-panel-header>
                          <v-expansion-panel-content>
                            <v-row>
                              <v-col cols="6">
                                <v-text-field
                                  v-model="value.name"
                                  label="Name"
                                  placeholder="Batátové hranolky"
                                  rounded
                                  filled
                                  hide-details
                                ></v-text-field>
                              </v-col>
                              <v-col cols="3">
                                <v-text-field
                                  v-model="value.price"
                                  type="number"
                                  label="Price"
                                  rounded
                                  filled
                                  hide-details
                                ></v-text-field>
                              </v-col>
                              <v-col cols="3">
                                <v-text-field
                                  v-model="value.maximumAmount"
                                  type="number"
                                  label="Maximum amount"
                                  rounded
                                  filled
                                  hide-details
                                ></v-text-field>
                              </v-col>
                            </v-row>
                          </v-expansion-panel-content>
                        </v-expansion-panel>
                      </v-expansion-panels>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <div class="flex-grow-1"></div>
                <v-btn color="primary" text rounded @click="close">Close</v-btn>
                <v-btn color="success" text rounded @click="save">Save</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:no-data>
        <v-btn color="primary" @click="initialize">Reset</v-btn>
      </template>
    </v-data-table>
  </v-skeleton-loader>
</template>

<script>
import restaurantService from "../services/restaurant.service";

export default {
  data: () => ({
    loading: true,
    dialog: false,
    headers: [
      { text: "Name", value: "name" },
      {
        text: "Minimum selections",
        value: "minimumSelections",
        sortable: false,
      },
      {
        text: "Maximum selections",
        value: "maximumSelections",
        sortable: false,
      },
      { text: "Actions", value: "action", sortable: false },
    ],
    items: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      minimumSelections: 0,
      maximumSelections: 1,
      values: [
        {
          name: "Batátové hranolky",
          price: 2,
          maximumAmount: 1,
        }
      ],
    },
    defaultItem: {
      name: "",
      minimumSelections: 0,
      maximumSelections: 1,
      values: [
        {
          name: "Batátové hranolky",
          price: 2,
          maximumAmount: 1,
        },
      ],
    },
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "Add new option" : "Edit option";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
  },

  created() {
    this.initialize();
  },

  methods: {
    async initialize() {
      const resp = await restaurantService.getRestaurant(
        localStorage.getItem("restaurantId"),
        "options"
      );
      console.log(resp);
      this.items = resp;
      this.loading = false;
    },
    editItem(item) {
      this.editedIndex = this.items.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    addOptionValue() {
      this.editedItem.values.push({
        name: "Batátové hranolky",
        price: 2,
        maximumAmount: 1,
      });
    },
    removeOptionValue(value) {
      this.editedItem.values = this.editedItem.values.filter((e) => e != value);
    },
    async deleteItem(item) {
      if (window.confirm("Are you sure you want to delete this option?")) {
        const resp = await restaurantService.removeOption(
          localStorage.getItem("restaurantId"),
          item._id
        );
        this.items = resp;
      }
    },
    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },
    async save() {
      if (this.editedIndex > -1) {
        const resp = await restaurantService.editOption(
          localStorage.getItem("restaurantId"),
          this.editedItem
        );
        this.items = resp;
      } else {
        const resp = await restaurantService.addOption(
          localStorage.getItem("restaurantId"),
          this.editedItem
        );
        this.items = resp;
      }
      this.close();
    },
  },
};
</script>

<style scoped>
.active-category-class {
  font-weight: bold;
}

.v-data-table .v-toolbar__content {
  float: left;
  padding: 0;
}

.v-btn--round .v-btn__content .v-icon {
  color: unset !important;
}
</style>