<template>
  <v-skeleton-loader
    :loading="loading"
    type="article, list-item-two-line@2, actions"
  >
    <v-container>
      <v-row>
        <v-card-title class="headline" style="color: black">
          Show transactions
          <v-chip-group
            mandatory
            active-class="primary--text"
            style="margin-left: 8px"
          >
            <v-chip @click="generateChart('hourly')">hourly</v-chip>
            <v-chip @click="generateChart('daily')">daily</v-chip>
            <v-chip @click="generateChart('weekly')">weekly</v-chip>
            <v-chip @click="generateChart('monthly')">monthly</v-chip>
          </v-chip-group>
        </v-card-title>
      </v-row>
      <v-row>
        <v-col cols="12" md="6">
          <v-card
            color="secondary"
            elevation="24"
            style="position: relative; min-height: 350px"
          >
            <div
              style="position: absolute; top: 0px; padding: 16px; color: white"
            >
              <v-icon color="primary" style="margin-bottom: 8px"
                >mdi-currency-usd-circle-outline</v-icon
              >
              <h2>{{ remainingRestaurantCredit.toFixed(2) }} €</h2>
              <span color="white">Remaining restaurant credit</span>
            </div>
            <div style="position: absolute; bottom: 0px; width: 100%">
              <apexchart
                height="250"
                type="area"
                :options="chartOptions"
                :series="series"
              ></apexchart>
            </div>
          </v-card>
        </v-col>
        <v-col cols="12" md="6">
          <v-card>
            <div class="pa-4" style="position: absolute; width: 100%">
              <v-card-title class="pa-0 mb-4">
                Top up your restaurant credit
                <v-btn
                  @click="addFunds(amount)"
                  style="margin-left: 8px"
                  color="success"
                  :disabled="!valid"
                  text
                  rounded
                  >Pay</v-btn
                >
              </v-card-title>
              <v-form ref="form" v-model="valid">
                <v-text-field
                  v-model="amount"
                  label="Amount"
                  type="number"
                  placeholder="100"
                  hint="Billed in restaurant's currency"
                  :rules="[
                    (v) => !!v || 'This field is required',
                    (v) =>
                      Number.isInteger(Number(v)) ||
                      'Must be an integer number',
                  ]"
                  required
                  persistent-hint
                  rounded
                  filled
                ></v-text-field>
              </v-form>
            </div>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-card elevation="24" flat>
            <v-card-title>Transaction history</v-card-title>
            <v-data-table
              :headers="headers"
              :items="transactions"
              sort-by="created"
              :sort-desc="true"
            >
              <template v-slot:item.created="{ item }">{{
                new Date(item.created).toLocaleDateString(undefined, {
                  weekday: "long",
                  year: "numeric",
                  month: "long",
                  day: "numeric",
                  hour: "2-digit",
                  minute: "2-digit",
                })
              }}</template>
              <template v-slot:item.amount="{ item }">
                <v-chip v-if="Number(item.amount) > 0" color="success"
                  >{{ item.amount }} €</v-chip
                >
                <v-chip v-if="Number(item.amount) < 0" color="error"
                  >{{ item.amount }} €</v-chip
                >
                <v-chip v-if="Number(item.amount) === 0"
                  >{{ item.amount }} €</v-chip
                >
              </template>
            </v-data-table>
          </v-card>
        </v-col>
      </v-row>
      <v-snackbar v-model="snackbar" :color="snackbarColor">
        {{ snackbarText }}
      </v-snackbar>
    </v-container>
  </v-skeleton-loader>
</template>

<script>
// import ApexCharts from "apexcharts";
import transactionService from "../services/transaction.service";
import restaurantService from "../services/restaurant.service";
import chartService from "../services/chart.service";

export default {
  data: () => ({
    loading: true,
    stripe: window.Stripe(
      "pk_test_51HxU33DxFtdn9X2Rb5p99UR21ES9wQI1CT1s61Ll9Ja9pNxol5l0Vq1e0gMeKRIOhMlhQLvm5f6poevAVEEPQc6J00C8HaZlap"
    ),
    amount: null,
    valid: true,
    snackbar: false,
    snackbarText: null,
    snackbarColor: null,
    remainingRestaurantCredit: 0,
    orderFee: null,
    transactions: [],
    remainingRestaurantCreditChartData: [],
    chartOptions: {
      chart: {
        type: "area",
        toolbar: {
          show: false,
        },
        zoom: {
          enabled: false,
        },
      },
      stroke: {
        show: true,
        curve: "smooth",
        lineCap: "round",
        width: 5,
      },
      colors: ["#0360ec"], //#ffaf40
      fill: {
        colors: ["#0360ec"],
        type: "gradient",
        gradient: {
          colorStops: [
            {
              offset: 50,
              color: "#100f0f",
              opacity: 0.0,
            },
            {
              offset: 100,
              color: "#0360ec",
              opacity: 0.55,
            },
          ],
        },
      },
      dataLabels: {
        enabled: false, // hodnoty na grafe
      },
      grid: {
        padding: {
          left: 0,
          right: 0,
        },
        show: false,
      },
      yaxis: {
        show: false,
      },
      tooltip: {
        enabled: true,
        x: {
          show: false,
        },
        marker: false,
      },
      xaxis: {
        type: "datetime",
        labels: {
          datetimeUTC: false,
          show: true, // x os- cas
          offsetY: 0,
          style: {
            colors: "#fff",
          },
        },
        axisTicks: {
          show: false,
        },
        tooltip: {
          enabled: false,
        },
        axisBorder: {
          show: false,
        },
      },
    },
    series: [
      {
        name: "Restaurant credit",
        data: [],
      },
    ],
    // options: {
    //   chart: {
    //     id: "mychart",
    //     height: 250,
    //     type: "area",
    //     toolbar: {
    //       show: false
    //     },
    //     zoom: {
    //       enabled: false
    //     },
    //     animations: {
    //       initialAnimation: {
    //         enabled: true
    //       }
    //     }
    //   },
    //   series: [
    //     {
    //       name: "Restaurant credit",
    //       data: []
    //     }
    //   ],
    //   colors: ["#0360ec"], //#ffaf40
    //   fill: {
    //     colors: ["#0360ec"],
    //     type: "gradient",
    //     gradient: {
    //       shadeIntensity: 0,
    //       opacityFrom: 0.55,
    //       opacityTo: 0.0,
    //       stops: [5, 90, 100]
    //     }
    //   },
    //   dataLabels: {
    //     enabled: false // hodnoty na grafe
    //   },
    //   grid: {
    //     padding: {
    //       left: 0,
    //       right: 0
    //     },
    //     show: false
    //   },
    //   yaxis: {
    //     show: false
    //   },
    //   tooltip: {
    //     enabled: true,
    //     x: {
    //       show: false
    //     },
    //     marker: false
    //   },
    //   xaxis: {
    //     type: "datetime",
    //     labels: {
    //       show: true, // x os- cas
    //       offsetY: 0,
    //       style: {
    //         colors: "#fff"
    //       }
    //     },
    //     axisBorder: {
    //       show: false
    //     },
    //     axisTicks: {
    //       show: false
    //     },
    //     tooltip: {
    //       enabled: false
    //     }
    //   }
    // },
    headers: [
      {
        text: "Date",
        value: "created",
      },
      { text: "Amount", value: "amount" },
    ],
  }),
  created() {
    this.fetchData();

    const params = new URLSearchParams(window.location.search);
    if (params.get("status") === "success") {
      this.snackbarText = `Thanks for topping up your credit. Successfully added ${params.get(
        "amount"
      )}.`;
      this.snackbarColor = "success";
      this.snackbar = true;
    } else if (params.get("status") === "error") {
      this.snackbarText = `Your payment was not processed correctly.`;
      this.snackbarColor = "error";
      this.snackbar = true;
    }
  },
  methods: {
    async fetchData() {
      const resp = await transactionService.getTransactions(
        localStorage.getItem("restaurantId")
      );
      this.transactions = resp.transactions;
      this.orderFee = resp.orderFee;
      this.remainingRestaurantCredit = resp.remainingRestaurantCredit;
      this.generateChart("hourly");
      this.loading = false;
    },
    async generateChart(timePeriod) {
      const remainingRestaurantCreditChartData = await chartService.generateChartData(
        this.remainingRestaurantCredit,
        this.orderFee,
        this.transactions,
        "credits",
        timePeriod
      );

      this.series = [
        {
          data: remainingRestaurantCreditChartData,
        },
      ];
    },
    async addFunds(amount) {
      const resp = await restaurantService.addFunds(
        localStorage.getItem("restaurantId"),
        Number(amount)
      );

      this.stripe.redirectToCheckout({
        sessionId: resp.sessionId,
      });
    },
  },
};
</script>

<style>
.apexcharts-inner .apexcharts-graphical {
  transform: translate(0, -10);
}
</style>
